import React, { Component } from 'react';
import * as Utils from './../utils.js';
import TaxtaxCalculator from '../lib/TaxCalculator';

import { connect } from 'react-redux'

class ResultView extends Component {
    render() {
        const ResultRow = (props) => {
            const {label, value, id} = props;
            return (
                <div className="row" id={id}>
                    <div className="col-md-6 col-xs-6">{label}</div>
                    <div className="col-md-6 col-xs-6">{value}</div>
                </div>
            )
        };
        
        const { fields } = this.props;

        if (!fields) {
            return (<div/>)
        }

        const taxCalculator = new TaxtaxCalculator(fields.av, fields.rate, fields.USD2MMK, fields.vehicleValueInMMK, fields.slipValue);

        const totalTaxes = Utils.formatNumber(taxCalculator.totalTaxesMMK());
        const totalCost = Utils.formatNumber(taxCalculator.grandTotalMMK());
        
        return (
            <div className="col-md-5">
                <h2>ကျသင့်တန်ဖိုးများ</h2>
                <div>
                    <ResultRow label="အကောက်ခွန်" value={"$"+Utils.formatNumber(taxCalculator.customDuty())}/>
                    <ResultRow label="အထူးကုန်စည်ခွန်" value={"$"+Utils.formatNumber(taxCalculator.specialGoodsTax())}/>
                    <ResultRow label="ကုန်သွယ်ခွန်" value={"$"+Utils.formatNumber(taxCalculator.commercialTax())}/>
                    <hr/>
                    <ResultRow label="ဆိပ်ကမ်းခွန်" value={Utils.formatNumber(taxCalculator.sateKanTaxInMMK()) + " ကျပ် / $" + Utils.formatNumber(taxCalculator.sateKanTax())}/>
                    <hr/>
                    <ResultRow label="ဝင်ငွေခွန်" value={Utils.formatNumber(taxCalculator.incomeTax()) + " ကျပ် (" + taxCalculator.incomeTaxRate() + '%)'}/>
                    <hr/>
                    <ResultRow label="ကညနခွန်" value={"$"+Utils.formatNumber(taxCalculator.kaNyaNaTax())}/>
                    <hr/>
                    <ResultRow label="ဆိပ်ကမ်းခွန် + ဝင်ငွေခွန် + ကညနခွန်" value={totalTaxes + " ကျပ်"}/>
                    <hr/>
                    <div className="row">
                        <div className="col-md-12">
                            <div className="row">
                                <div className="col-md-10">
                                    <h3>စုစုပေါင်း</h3>
                                    <label>အခွန်စုစုပေါင်း + ကားတန်ဖိုး + အပ်ကား (စလစ်)</label>
                                    <p className="totalCost text-center">{totalCost} ကျပ်</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        fields: state.calculator.fields
    }
};

ResultView = connect(mapStateToProps)(ResultView);

export default ResultView;


